.form-input {
  box-sizing: border-box;
  padding: 19px 40px;
  border-radius: 80px;
  border: 2px solid #E3E3E3;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  color: $color-black;
  opacity: 0.87;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;

  &:focus {
    color: $color-black;
    opacity: 0.87;
    border: 2px solid $color-yellow;
  }

  &:invalid:not(:placeholder-shown) {
    border: 2px solid #FF0000;
  }

  &:valid:not(:placeholder-shown)  {
    border: 2px solid $color-black;
  }

  &::placeholder {
    opacity: 0.5;
  }
}

.form input[type="checkbox"] {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.form input[type="checkbox"] + label {
  display: block;
  position: relative;
  cursor: pointer;
}

.form input[type="checkbox"] + label::before {
  @include pseudo;
  width: 12px;
  height: 12px;
  border-radius: 2px;
  border: 2px solid $color-yellow;
  top: 0;
  left: 0;
}

.form input[type="checkbox"]:checked + label::after {
  @include pseudo;
  background-image: url("../img/form-checking.svg");
  background-repeat: no-repeat;
  width: 15px;
  height: 15px;
  top: 0px;
  left: 0px;
}
