.icon-social {
  width: 24px;
  height: 24px;
  opacity: 0.16;

  &--white {
    fill: #ffffff;
    opacity: 1;
  }
  
  &:hover {
    opacity: 0.5;
  }
}

.icon-social__mention {
  @include pseudo;
  top: 17px;
  left: 40px;
}

@media #{$mobile} {
  .icon-social--footer {
    width: 32px;
    height: 32px;
  }
}
